import { authInstance } from "../axios";

/**
 * User login
 * @param User **Thng tin ng nhp**
 *
 * - `username`: Tn ngi dng
 * - `password`: Mt khu
 */
const authLogin = async (user) => {
  return authInstance.post(`login`, user);
};

/**
 * @param User **ng k ti khon**
 *
 * - `username`: tn ngi dng
 * - `password`: mt khu
 * - `displayName`: h v tn
 * - `email`: email ng k
 *
 * > Note: xem validate d liu ti: `useraggregate.services.UserServiceImpl.register()`
 */
const registerUser = async (user) => {
  return authInstance.post(`signup`, user);
};

export { authLogin, registerUser };
