import { contextInstance } from "../axios";

/**
 * Create new user
 * @param User **ngi dng**
 *
 * - `username`: tn ngi dng
 * - `password`: mt khu
 * - `displayName`: tn hin th
 * - `birthday`: ngy sinh
 * - `avatar`: nh i din
 * - `profile`: Thng tin c nhn
 * - `gender`: Gii tnh
 * - `role`: Vai tr
 */
const createUser = async (user) => {
  return contextInstance.post(`common/user`, user);
};

/** Get friend for tag post */
const searchUserForPost = async (search) => {
  return contextInstance.get(
    `common/user/search-users-for-post?username.contains=${search}`
  );
};

/**
 * Get all users
 * @return
 *
 * - `username`: tn ngi dng
 * - `password`: mt khu
 * - `displayName`: tn hin th
 * - `birthday`: ngy sinh
 * - `avatar`: nh i din
 * - `profile`: Thng tin c nhn
 * - `gender`: Gii tnh
 * - `role`: Vai tr
 */
const listUsers = async () => {
  return contextInstance.get(`common/user`);
};

/**
 * Get user by `id`
 * @param id id ngi dng
 * @return
 *
 * - `username`: tn ngi dng
 * - `password`: mt khu
 * - `displayName`: tn hin th
 * - `birthday`: ngy sinh
 * - `avatar`: nh i din
 * - `profile`: Thng tin c nhn
 * - `gender`: Gii tnh
 * - `role`: Vai tr
 */
const getUserById = async (id) => {
  return contextInstance.get(`common/user/${id}`);
};

/**
 * Get user by `username`
 * @param username username
 *  ngi dng
 * @return
 *
 * - `username`: tn ngi dng
 * - `password`: mt khu
 * - `displayName`: tn hin th
 * - `birthday`: ngy sinh
 * - `avatar`: nh i din
 * - `profile`: Thng tin c nhn
 * - `gender`: Gii tnh
 * - `role`: Vai tr
 */
const getUserByUsername = async (username) => {
  return contextInstance.get(`common/user`, {
    params: {
      "username.equal": username,
    },
  });
};

/**
 * Update user by `user.id`
 * @param User **ngi dng**
 *
 * - `username`: tn ngi dng
 * - `password`: mt khu
 * - `displayName`: tn hin th
 * - `birthday`: ngy sinh
 * - `avatar`: nh i din
 * - `profile`: Thng tin c nhn
 * - `gender`: Gii tnh
 * - `role`: Vai tr
 */
const updateUser = async (user, id) => {
  return contextInstance.put(`common/user/${id}/update`, user);
};

/**
 * Delete user by `user.id`
 * @param User **ngi dng**
 */
const deleteUser = async (user) => {
  return contextInstance.delete(`common/user/${user.id}`);
};

const uploadUserImg = async (image) => {
  const formData = new FormData();
  formData.append("avatarFile", image);
  const config = {
    headers: {
      "content-type": "multipart/form-data",
    },
  };
  return contextInstance.put(`common/user/update-avatar`, formData, config);
};

const changePassword = async (id, password) => {
  contextInstance.put(`/common/user/${id}/change-password`, password);
};

export {
  createUser,
  updateUser,
  getUserById,
  listUsers,
  deleteUser,
  searchUserForPost,
  getUserByUsername,
  uploadUserImg,
  changePassword,
};
